
.vue-color__sketch {
  position: relative;
  width: 200px;
  padding: 10px 10px 0;
  box-sizing: initial;
  background: #fff;
  border-radius: 4px;
  box-shadow: 0 0 0 1px rgba(0,0,0,0.15), 0 8px 16px rgba(0,0,0,0.15);
}
.vue-color__sketch__saturation-wrap {
  width: 100%;
  padding-bottom: 75%;
  position: relative;
  overflow: hidden;
}
.vue-color__sketch__controls {
  display: flex;
}
.vue-color__sketch__sliders {
  padding: 4px 0;
  flex: 1;
}
.vue-color__sketch__sliders .vue-color__c-hue,
.vue-color__sketch__sliders .vue-color__c-alpha__gradient {
  border-radius: 2px;
}
.vue-color__sketch__hue-wrap {
  position: relative;
  height: 10px;
}
.vue-color__sketch__alpha-wrap {
  position: relative;
  height: 10px;
  margin-top: 4px;
  overflow: hidden;
}
.vue-color__sketch__color-wrap {
  width: 24px;
  height: 24px;
  position: relative;
  margin-top: 4px;
  margin-left: 4px;
  border-radius: 3px;
}
.vue-color__sketch__active-color {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  border-radius: 2px;
  box-shadow: inset 0 0 0 1px rgba(0,0,0,0.15), inset 0 0 4px rgba(0,0,0,0.25);
  z-index: 2;
}
.vue-color__sketch__field {
  display: flex;
  padding-top: 4px;
}
.vue-color__sketch__field .vue-color__editable-input__input {
  width: 80%;
  padding: 4px 10% 3px;
  border: none;
  box-shadow: inset 0 0 0 1px #ccc;
  font-size: 11px;
}
.vue-color__sketch__field .vue-color__editable-input__label {
  display: block;
  text-align: center;
  font-size: 11px;
  color: #222;
  padding-top: 3px;
  padding-bottom: 4px;
  text-transform: capitalize;
}
.vue-color__sketch__field--single {
  flex: 1;
  padding-left: 6px;
}
.vue-color__sketch__field--double {
  flex: 2;
}
.vue-color__sketch__presets {
  margin-right: -10px;
  margin-left: -10px;
  padding-left: 10px;
  padding-top: 10px;
  border-top: 1px solid #eee;
}
.vue-color__sketch__presets-color {
  border-radius: 3px;
  overflow: hidden;
  position: relative;
  display: inline-block;
  margin: 0 10px 10px 0;
  vertical-align: top;
  cursor: pointer;
  width: 16px;
  height: 16px;
  box-shadow: inset 0 0 0 1px rgba(0,0,0,0.15);
}


