
.vue-color__c-alpha {
  position: absolute;
  top: 0px;
  right: 0px;
  bottom: 0px;
  left: 0px;
}
.vue-color__c-alpha__checkboard-wrap {
  position: absolute;
  top: 0px;
  right: 0px;
  bottom: 0px;
  left: 0px;
  overflow: hidden;
}
.vue-color__c-alpha__gradient {
  position: absolute;
  top: 0px;
  right: 0px;
  bottom: 0px;
  left: 0px;
}
.vue-color__c-alpha__container {
  cursor: pointer;
  position: relative;
  z-index: 2;
  height: 100%;
  margin: 0 3px;
}
.vue-color__c-alpha__pointer {
  z-index: 2;
  position: absolute;
}
.vue-color__c-alpha__picker {
  cursor: pointer;
  width: 4px;
  border-radius: 1px;
  height: 8px;
  box-shadow: 0 0 2px rgba(0,0,0,0.6);
  background: #fff;
  margin-top: 1px;
  transform: translateX(-2px);
}


