
.vue-colors__photoshop {
  background: #dcdcdc;
  border-radius: 4px;
  box-shadow: 0 0 0 1px rgba(0,0,0,0.25), 0 8px 16px rgba(0,0,0,0.15);
  box-sizing: initial;
  width: 513px;
  font-family: Roboto;
}
.vue-colors__photoshop__head {
  background-image: linear-gradient(-180deg, #f0f0f0 0%, #d4d4d4 100%);
  border-bottom: 1px solid #b1b1b1;
  box-shadow: inset 0 1px 0 0 rgba(255,255,255,0.2), inset 0 -1px 0 0 rgba(0,0,0,0.02);
  height: 23px;
  line-height: 24px;
  border-radius: 4px 4px 0 0;
  font-size: 13px;
  color: #4d4d4d;
  text-align: center;
}
.vue-colors__photoshop__body {
  padding: 15px;
  display: flex;
}
.vue-colors__photoshop__saturation-wrap {
  width: 256px;
  height: 256px;
  position: relative;
  border: 2px solid #b3b3b3;
  border-bottom: 2px solid #f0f0f0;
  overflow: hidden;
}
.vue-colors__photoshop__saturation-wrap .vue-color__saturation--circle {
  width: 12px;
  height: 12px;
}
.vue-colors__photoshop__hue-wrap {
  position: relative;
  height: 256px;
  width: 19px;
  margin-left: 10px;
  border: 2px solid #b3b3b3;
  border-bottom: 2px solid #f0f0f0;
}
.vue-colors__photoshop__hue-pointer {
  position: relative;
}
.vue-colors__photoshop__hue-pointer--left,
.vue-colors__photoshop__hue-pointer--right {
  position: absolute;
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 5px 0 5px 8px;
  border-color: transparent transparent transparent #555;
}
.vue-colors__photoshop__hue-pointer--left:after,
.vue-colors__photoshop__hue-pointer--right:after {
  content: "";
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 4px 0 4px 6px;
  border-color: transparent transparent transparent #fff;
  position: absolute;
  top: 1px;
  left: 1px;
  transform: translate(-8px, -5px);
}
.vue-colors__photoshop__hue-pointer--left {
  transform: translate(-13px, -4px);
}
.vue-colors__photoshop__hue-pointer--right {
  transform: translate(20px, -4px) rotate(180deg);
}
.vue-colors__photoshop__controls {
  width: 180px;
  margin-left: 10px;
  display: flex;
}
.vue-colors__photoshop__actions {
  margin-left: 20px;
  flex: 1;
}
.vue-colors__photoshop__ac-btn {
  cursor: pointer;
  background-image: linear-gradient(-180deg, #fff 0%, #e6e6e6 100%);
  border: 1px solid #878787;
  border-radius: 2px;
  height: 20px;
  box-shadow: 0 1px 0 0 #eaeaea;
  font-size: 14px;
  color: #000;
  line-height: 20px;
  text-align: center;
  margin-bottom: 10px;
}
.vue-colors__photoshop__previews {
  width: 60px;
}
.vue-colors__photoshop__previews__swatches {
  border: 1px solid #b3b3b3;
  border-bottom: 1px solid #f0f0f0;
  margin-bottom: 2px;
  margin-top: 1px;
}
.vue-colors__photoshop__previews__pr-color {
  height: 34px;
  box-shadow: inset 1px 0 0 #000, inset -1px 0 0 #000, inset 0 1px 0 #000;
}
.vue-colors__photoshop__previews__label {
  font-size: 14px;
  color: #000;
  text-align: center;
}
.vue-colors__photoshop__fields {
  padding-top: 5px;
  padding-bottom: 9px;
  width: 80px;
  position: relative;
}
.vue-colors__photoshop__fields .vue-color__editable-input__input {
  margin-left: 40%;
  width: 40%;
  height: 18px;
  border: 1px solid #888;
  box-shadow: inset 0 1px 1px rgba(0,0,0,0.1), 0 1px 0 0 #ececec;
  margin-bottom: 5px;
  font-size: 13px;
  padding-left: 3px;
  margin-right: 10px;
}
.vue-colors__photoshop__fields .vue-color__editable-input__label {
  top: 0;
  left: 0;
  width: 34px;
  text-transform: uppercase;
  font-size: 13px;
  height: 18px;
  line-height: 22px;
  position: absolute;
}
.vue-colors__photoshop__fields__divider {
  height: 5px;
}
.vue-colors__photoshop__fields__hex .vue-color__editable-input__input {
  margin-left: 20%;
  width: 80%;
  height: 18px;
  border: 1px solid #888;
  box-shadow: inset 0 1px 1px rgba(0,0,0,0.1), 0 1px 0 0 #ececec;
  margin-bottom: 6px;
  font-size: 13px;
  padding-left: 3px;
}
.vue-colors__photoshop__fields__hex .vue-color__editable-input__label {
  position: absolute;
  top: 0;
  left: 0;
  width: 14px;
  text-transform: uppercase;
  font-size: 13px;
  height: 18px;
  line-height: 22px;
}


