
.vue-color__chrome {
  background: #fff;
  border-radius: 2px;
  box-shadow: 0 0 2px rgba(0,0,0,0.3), 0 4px 8px rgba(0,0,0,0.3);
  box-sizing: initial;
  width: 225px;
  font-family: Menlo;
  background-color: #fff;
}
.vue-color__chrome__controls {
  display: flex;
}
.vue-color__chrome__color-wrap {
  width: 32px;
}
.vue-color__chrome__active-color {
  margin-top: 6px;
  width: 16px;
  height: 16px;
  border-radius: 8px;
  position: relative;
  overflow: hidden;
}
.vue-color__chrome__sliders {
  flex: 1;
}
.vue-color__chrome__sliders .vue-color__c-hue,
.vue-color__chrome__sliders .vue-color__c-alpha__gradient {
  border-radius: 2px;
}
.vue-color__chrome__sliders .vue-color__c-alpha__picker,
.vue-color__chrome__sliders .vue-color__c-hue__picker {
  width: 12px;
  height: 12px;
  border-radius: 6px;
  transform: translate(-6px, -2px);
  background-color: #f8f8f8;
  box-shadow: 0 1px 4px 0 rgba(0,0,0,0.37);
}
.vue-color__chrome__fields-wrap {
  padding-top: 16px;
  display: flex;
}
.vue-color__chrome__fields {
  display: flex;
  margin-left: -6px;
  flex: 1;
}
.vue-color__chrome__field {
  padding-left: 6px;
  width: 100%;
}
.vue-color__chrome__toggle-btn {
  width: 32px;
  text-align: right;
  position: relative;
}
.vue-color__chrome__icon {
  margin-right: -4px;
  margin-top: 12px;
  cursor: pointer;
  position: relative;
  z-index: 2;
}
.vue-color__chrome__icon-highlight {
  position: absolute;
  width: 24px;
  height: 28px;
  background: #eee;
  border-radius: 4px;
  top: 10px;
  left: 12px;
}
.vue-color__chrome__hue-wrap {
  position: relative;
  height: 10px;
  margin-bottom: 8px;
}
.vue-color__chrome__alpha-wrap {
  position: relative;
  height: 10px;
}
.vue-color__chrome__chrome-body {
  padding: 16px 16px 12px;
  background-color: #fff;
}
.vue-color__chrome__saturation-wrap {
  width: 100%;
  padding-bottom: 55%;
  position: relative;
  border-radius: 2px 2px 0 0;
  overflow: hidden;
}
.vue-color__chrome__saturation-wrap .vue-color__saturation--circle {
  width: 12px;
  height: 12px;
}
.vue-color__chrome__fields .vue-color__editable-input__input {
  font-size: 11px;
  color: #333;
  width: 100%;
  border-rradius: 2px;
  border: none;
  box-shadow: inset 0 0 0 1px #dadada;
  height: 21px;
  text-align: center;
}
.vue-color__chrome__fields .vue-color__editable-input__label {
  text-transform: uppercase;
  font-size: 11px;
  line-height: 11px;
  color: #969696;
  text-align: center;
  display: block;
  margin-top: 12px;
}


